import React, { Component } from "react";
import Popconfirms from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  Col,
  Row,
  Divider,
  TreeSelect,
  Alert
} from "antd";
import getLanguage from "../../../App/MyLanguange"

// const { Option } = Select;
// const { TextArea } = Input;
const InputGroup = Input.Group;

class AssetCategoryForm extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props.assetCategory);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { user } = this.props.auth;
    const assetCategory = this.props.assetCategory ? this.props.assetCategory : [];
    const { loadingSubmit, loading } = assetCategory;
    const { activate, deactivate } = this.props;
    let data = assetCategory.asset_category ? assetCategory.asset_category : [];
    //console.log(data);
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const used = data.is_used;
    const error = assetCategory.errorField ? assetCategory.errorField : [];

    const loop = (array, except_id) =>
      array.map(data => {
        let perData
        if (data.is_active === 0) {
          perData = {
            title: data.asset_category_code + " (" + data.asset_category_name + ")",
            value: data.id_asset_category,
            key: data.id_asset_category,
            disabled: true
          }
        }
        else if (data.id_asset_category != except_id) {
          perData = {
            title: data.asset_category_code + " (" + data.asset_category_name + ")",
            value: data.id_asset_category,
            key: data.id_asset_category,
            children: data.children ? loop(data.children, except_id) : null
          }
        }
        else {
          perData = {
            title: data.asset_category_code + " (" + data.asset_category_name + ")",
            value: data.id_asset_category,
            key: data.id_asset_category,
            disabled: true
          }
        }
        return perData
      })



    const formAssetLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormAssetLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="assetCategories.update.subtitle.data" />
        </Divider>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="assetCategories.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : null}

        <Row>
          <Col span={24}>

            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="assetCategories.parent" />}
            >
              {getFieldDecorator("id_asset_category_parent", {
                initialValue: data.id_asset_category_parent,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <TreeSelect
                  style={{ width: "100%" }}
                  allowClear={true}
                  showSearch={true}
                  treeNodeFilterProp="title"
                  dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
                  treeData={loop(this.props.assetCategory.dropdown_update, data.id_asset_category)}
                  treeDefaultExpandAll
                // onChange={this.onChange}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="assetCategories.code" />}
              validateStatus={
                error.asset_category_code || getFieldError("asset_category_code")
                  ? "error"
                  : null
              }
              help={
                error.asset_category_code
                  ? error.asset_category_code
                  : getFieldError("asset_category_code")
              }
            >
              {getFieldDecorator("asset_category_code", {
                initialValue: data.asset_category_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  // addonBefore={prefix}
                  disabled={used ? true : false}
                />
              )}
            </Form.Item>


            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="assetCategories.name" />}
            >
              {getFieldDecorator("asset_category_name", {
                initialValue: data.asset_category_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input
                disabled={used ? true : false}
              />)}
            </Form.Item>

          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <InputGroup compact>
              <Col>
                <Form.Item {...tailFormAssetLayout}>
                  <Button
                    type="primary"
                    htmlType="submit"
                    style={{ marginRight: "15px" }}
                    loading={loadingSubmit ? true : false}
                  >
                    <IntlMessages id="button.submit" />
                  </Button>
                </Form.Item>
              </Col>
              <Col>
                {data.is_active ? (
                  <Form.Item {...tailFormAssetLayout}>
                    <Popconfirms
                      title={
                        <IntlMessages id="assetCategories.popconfirm.deactivate" />
                      }
                      onConfirm={e => {
                        e.preventDefault();
                        deactivate(data.id_asset_category);
                      }}
                      okText={getLanguage({ id: "yes" })}
                      cancelText={getLanguage({ id: "no" })}
                    >
                      <Button
                        type="danger"
                        loading={
                          loadingSubmit || loading ? true : false
                        }
                        disabled={used ? true : false}
                      >
                        <IntlMessages id="tooltip.deactivate" />
                      </Button>
                    </Popconfirms>
                  </Form.Item>
                ) : (
                    <Form.Item {...tailFormAssetLayout}>
                      <Popconfirms
                        title={
                          <IntlMessages id="assetCategories.popconfirm.activate" />
                        }
                        onConfirm={e => {
                          e.preventDefault();
                          activate(data.id_asset_category);
                        }}
                        okText={getLanguage({ id: "yes" })}
                        cancelText={getLanguage({ id: "no" })}
                      >
                        <Button
                          type="default"
                          style={{ backgroundColor: "#00B16A", color: "#FFFFFF", }}
                          loading={
                            loadingSubmit || loading ? true : false
                          }
                          disabled={used ? true : false}
                        >
                          <IntlMessages id="tooltip.activate" />
                        </Button>
                      </Popconfirms>
                    </Form.Item>
                  )}
              </Col>
            </InputGroup>
          </Col>
        </Row>
      </Form>
    );
  }
}

const WrappedAssetCategoryForm = Form.create({ name: "update_asset_category" })(AssetCategoryForm);

export default WrappedAssetCategoryForm;
