const actions = {
  getAssetCategories: () => ({
    type: "GET_ASSET_CATEGORIES_REQUEST",
  }),
  storeAssetCategory: body => ({
    type: "STORE_ASSET_CATEGORY_REQUEST",
    payload: { body: body }
  }),
  showAssetCategory: id_asset_category => ({
    type: "SHOW_ASSET_CATEGORY_REQUEST",
    payload: { id_asset_category: id_asset_category }
  }),
  updateAssetCategory: (id_asset_category, body) => ({
    type: "UPDATE_ASSET_CATEGORY_REQUEST",
    payload: { id_asset_category: id_asset_category, body: body }
  }),
  activateAssetCategory: id_asset_category => ({
    type: "ACTIVATE_ASSET_CATEGORY_REQUEST",
    payload: { id_asset_category: id_asset_category }
  }),
  deactivateAssetCategory: id_asset_category => ({
    type: "DEACTIVATE_ASSET_CATEGORY_REQUEST",
    payload: { id_asset_category: id_asset_category }
  }),
  clearAssetCategory: () => ({
    type: "CLEAR_ASSET_CATEGORY"
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
