import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import notification from "../../../../components/notification";
import {
  Input,
  // Select, 
  Tree,
  // Row, 
  Col, Divider, Empty, Spin
} from "antd";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetCategory/actions";
import CardWrapper, { Box } from "../../../App/app.style";
import CreateForm from "./createForm";
import UpdateForm from "./updateForm";
import getLanguage from "../../../App/MyLanguange"

// const Option = Select.Option;
const Search = Input.Search;
const { TreeNode } = Tree;
const redirectTo = "/dashboard/setup/menus";

// Searching Asset
const getParentKey = (key, tree) => {
  let parentKey;
  for (let i = 0; i < tree.length; i++) {
    const node = tree[i];
    if (node.children) {
      if (
        node.children.some(
          asset_category => asset_category.id_asset_category === key
        )
      ) {
        parentKey = node.id_asset_category;
      } else if (getParentKey(key, node.children)) {
        parentKey = getParentKey(key, node.children);
      }
    }
  }
  return parentKey;
};

class AssetCategory extends Component {
  state = {
    expandedKeys: [],
    searchValue: "",
    autoExpandParent: true,
    id_asset_category: null,
    status: 0
    // 0 empty , 1 create , 2 update
  };

  // Get All data diawal
  componentDidMount() {
    const { getAssetCategories } = this.props;
    getAssetCategories();
  }

  // Display notif
  componentDidUpdate() {
    // Display error notif
    if (
      !this.props.assetCategory.loadingSubmit &&
      !this.props.assetCategory.loadingAssetCategory &&
      !this.props.assetCategory.loadingAssetCategories &&
      this.props.assetCategory.error
    ) {
      notification("error", this.props.assetCategory.error);
      this.props.clearError();
    }

    // Display success notif
    if (
      !this.props.assetCategory.loadingSubmit &&
      !this.props.assetCategory.loadingAssetCategory &&
      !this.props.assetCategory.loadingAssetCategories &&
      this.props.assetCategory.success
    ) {
      notification("success", this.props.assetCategory.success);
      this.props.clearSuccess();

      // Clear state new AssetCategory
      this.setState({
        ...this.state,
        id_asset_category: this.props.assetCategory.asset_category
          ? this.props.assetCategory.asset_category.id_asset_category
          : null
      });
    }
  }

  // Handle change search
  handleSearchChange = e => {
    const {
      asset_categories_list,
      asset_categories
    } = this.props.assetCategory;
    const value = e.target.value;
    const expandedKeys = asset_categories_list.map(asset_category => {
      const name =
        asset_category.asset_category_code +
        " " +
        asset_category.asset_category_name;
      if (name.toLowerCase().indexOf(value.toLowerCase()) > -1) {
        return getParentKey(asset_category.id_asset_category, asset_categories);
      }
      return null;
    });

    this.setState({
      expandedKeys,
      searchValue: value,
      autoExpandParent: true
    });
  };

  // Handle Add Button
  handleAddButton = () => {
    const { clearAssetCategory } = this.props;
    this.setState({
      id_asset_category: null,
      status: 1
    });
    clearAssetCategory();
  };

  // Handle select tree
  handleSelectTree = e => {
    const { showAssetCategory } = this.props;
    this.props.clearErrorField();

    const value = e[0] ? e[0] : "";

    if (value) {
      showAssetCategory(value);
    }

    this.setState({
      ...this.state,
      id_asset_category: value ? value : null,
      status: value ? 2 : 0
    });
  };

  // Handle expand tree
  onExpand = expandedKeys => {
    this.setState({
      expandedKeys,
      autoExpandParent: false
    });
  };

  // Handle submit Create
  handleSubmitCreate = value => {
    const { storeAssetCategory } = this.props;
    storeAssetCategory(value);
  };

  handleSubmitUpdate = value => {
    const { updateAssetCategory } = this.props;
    const { id_asset_category } = this.state;
    updateAssetCategory(id_asset_category, value);
  };

  // Handle Activate asset_category
  handleActivate = id_asset_category => {
    const { activateAssetCategory } = this.props;
    // console.log(id_asset_category);
    activateAssetCategory(id_asset_category);
  };

  // Handle dectivate asset_category
  handleDeactivate = id_asset_category => {
    const { deactivateAssetCategory } = this.props;
    // console.log(id_asset_category);
    deactivateAssetCategory(id_asset_category);
  };

  render() {
    // console.log("/\\/");
    // console.log(this.state);
    const { asset_category, asset_categories } = this.props.assetCategory;
    const {
      loadingSubmit,
      loadingAssetCategory,
      loadingAssetCategories
    } = this.props.assetCategory;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const checkLoading = () => {
      if (loadingAssetCategories) {
        return true;
      }
      return false;
    };

    const {
      searchValue,
      expandedKeys,
      autoExpandParent
      // newAssetCategory
    } = this.state;

    const loop = data =>
      data.map(asset_category => {
        let name =
          asset_category.asset_category_code +
          " (" +
          asset_category.asset_category_name +
          ")";
        const key = name;
        const index = key.toLowerCase().indexOf(searchValue.toLowerCase());
        const beforeStr = name.substr(0, index);
        const str = name.substr(index, searchValue.length);
        const afterStr = name.substr(index + searchValue.length);
        const title =
          index > -1 ? (
            <span>
              {beforeStr}
              <span style={{ color: "#000000", fontWeight: "bold" }}>
                {str}
              </span>
              {afterStr}
            </span>
          ) : (
              <span>{name}</span>
            );
        if (asset_category.children) {
          return (
            <TreeNode
              key={asset_category.id_asset_category}
              title={
                asset_category.is_active === 0 ? (
                  <span style={{ color: "#f5222d" }}>{title}</span>
                ) : (
                    title
                  )
              }
            >
              {loop(asset_category.children)}
            </TreeNode>
          );
        }
        return (
          <TreeNode key={asset_category.id_asset_category} title={title} />
        );
      });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetCategories.pageHeader" />}
            style={{ margin: 0 }}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Asset Category") ? (
                <Button type="primary" onClick={this.handleAddButton}>
                  <IntlMessages id="assetCategories.add" />
                </Button>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            {/* <InputGroup compact> */}
            <Col span={12}>
              <Col>
                <Search
                  placeholder={getLanguage({ id: "assetServiceTypes.assetCategory" })}
                  disabled={checkLoading()}
                  allowClear
                  onChange={this.handleSearchChange}
                />
              </Col>
            </Col>

            {/* </InputGroup> */}
          </div>

          <CardWrapper>
            <Col span={12}>
              <Col span={23}>
                {checkLoading() ? (
                  <div className="spinStyle">
                    <Spin />
                  </div>
                ) : asset_categories.length === 0 ? (
                  <Empty description={<IntlMessages id="noData" />} />
                ) : (
                      <Tree
                        showIcon
                        showLine
                        onExpand={this.onExpand}
                        expandedKeys={expandedKeys}
                        autoExpandParent={autoExpandParent}
                        onSelect={this.handleSelectTree}
                      >
                        {loop(asset_categories)}
                      </Tree>
                    )}
              </Col>
              <Col span={1}>
                <Divider type="vertical" style={{ height: "100vh" }} />
              </Col>
            </Col>
            <Col span={12}>
              {loadingAssetCategory && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin />
                </div>
              ) : this.state.status === 2 ? (
                <UpdateForm
                  submit={this.handleSubmitUpdate}
                  assetCategory={this.props.assetCategory}
                  auth={this.props.auth}
                  activate={this.handleActivate}
                  deactivate={this.handleDeactivate}
                />
              ) : this.state.status === 1 ? (
                <CreateForm
                  submit={this.handleSubmitCreate}
                  assetCategory={this.props.assetCategory}
                  auth={this.props.auth}
                />
              ) : (
                      <Empty
                        description={
                          <IntlMessages id="assetCategories.noSelection" />
                        }
                      />
                    )}
            </Col>
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { assetCategory: state.assetCategory, auth: state.auth };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(AssetCategory);
